}

.label-container{
    display: flex;
    align-items: center;
    color: var(--settings-text-color);
}

.description-container{
    margin-left: 20px;
    font-size: 16px;
    font-size: var(--description-font-size);
}

input[type=checkbox] {
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
}

.custom-toggle{
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    width: 20px;
    width: var(--checkbox-size);
    height: 20px;
    height: var(--checkbox-size);
    border-radius: 4px;
    background-color: transparent;
    border: 2px solid var(--text-color);
}

/* hover */
.label-container:hover input ~ .custom-toggle {
    border: 2px solid var(--color-hover);
}

/* click */
.label-container input:active ~ .custom-toggle {
    -webkit-animation: border-click 0.1s;
            animation: border-click 0.1s;
    
}
  
/* checked */
.label-container input:checked ~ .custom-toggle {
    /* background-color: green; */
}

.custom-toggle:after {
    content: "";
    position: absolute;
    display: none;
}

.label-container input:checked ~ .custom-toggle:after {
    display: block;
}
  
/* checkmark */
.label-container .custom-toggle:after {
    width: 80%;
    height: 80%;
    border-radius: 2px;
    -webkit-animation: checkmark-grow 0.2s;
            animation: checkmark-grow 0.2s;
    background-color: var(--text-color);
}

@media only screen and (min-width: 1300px){
