}

@-webkit-keyframes checkmark-grow{
    0%{
        width: 0px;
        height: 0px
    }
    50%{
        width: 0px;
        height: 0px;
    }
    80%{
        width: 100%;
        height: 100%;
    }
    100%{
        width: 80%;
        height: 80%;
    }
}

@keyframes checkmark-grow{
    0%{
        width: 0px;
        height: 0px
    }
    50%{
        width: 0px;
        height: 0px;
    }
    80%{
        width: 100%;
        height: 100%;
    }
    100%{
        width: 80%;
        height: 80%;
    }
}

